*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
}
#canvas{
    display: block;
}
body {
    overflow: hidden;
}
.tools{
    position: fixed;
    left: 50%;
    bottom: 30px;
    margin-left: -160px;
    width: 320px;
    height: 50px;
    display: flex;
    justify-content: center;
}
.tools .container{
    padding: 8px 20px;
    border-radius: 40px;
    box-shadow: 0 1px 2px 0 rgba(32,33,36,0.28);
    background: #fff;
}
.tools .container li{
    display:inline-block;
}
.tools button{
    width: 30px;
    height: 30px;
    border: none;
    outline: none;
    background-size: 20px 20px;
    background-position: center center;
    background-repeat: no-repeat;
    background-color: #fff;
    margin: 0 6px;
    transition: 0.3s;
    cursor: pointer;
    border: 2px solid transparent;
}
.tools button.save{
    background-image: url('../image/save.png'); 
}
.tools button.brush{
    background-image: url('../image/pen.png'); 
}
.tools button.eraser{
    background-image: url('../image/eraser.png'); 
}
.tools button.clear{
    background-image: url('../image/clear.png'); 
}
.tools button.revocation{
    background-image: url('../image/revocation_before.png');
}
.tools button.back_revocation{
    background-image: url('../image/back_revocation_before.png');
}
.tools button.revocation.active{
    background-image: url('../image/revocation.png');
    border-color: transparent;
}
.tools button.back_revocation.active{
    background-image: url('../image/back_revocation.png');
    border-color: transparent;
}
.tools button.save:hover,
.tools button.brush:hover,
.tools button.eraser:hover,
.tools button.clear:hover,
.tools button.revocation:hover,
.tools button.back_revocation:hover {
    border-radius: 5px;
    background-color: #999;
}
.tools button.active{
    border-radius: 5px;
    border-color: #1398E6;
}
.pen-detail .closeBtn{
    position: absolute;
    top: 6px;
    right: 8px;
    width: 30px;
    height: 30px;
    background: url('../image/close.png') center center no-repeat;
    cursor: pointer;
}
.pen-detail{
    position: fixed;
    left: 50%;
    margin-left: -115px;
    bottom: 90px;
    width: 230px;
    height: 140px;
    padding: 20px 20px;
    border: 1px solid #81A4BD;
    border-radius: 5px;
    color: #808FA2;
    /*font-style: 18px;*/
    background: #fff;
    font-size: 14px;
    display: none;
}
.pen-detail.active{
    display: block;
}
.pen-detail p{
    margin-bottom: 5px;
}
.circle-box{
    line-height: 24px;
}
.circle-box{
    position: relative;
    width: 24px;
    height: 24px;
    display: inline-block;
    text-align: center;
    margin-right: 8px;
}
#thickness{
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -1px;
    margin-top: -1px;
    background: #000;
    border-radius: 50%;
    transform-origin: center;
    width: 2px;
    height: 2px;
}
input[type=range]{
    -webkit-appearance: none;
    width: 130px;
    height: 24px;
    outline: none;
}
input[type='range']::-webkit-slider-runnable-track{  
    background-color: #DBDBDB;
    height: 4px;
    border-radius: 5px;
}
input[type='range']::-webkit-slider-thumb {  
    -webkit-appearance: none;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background: #FF4081;
    cursor: pointer;
    margin-top: -4px;
}
.pen-color li{
    position: relative;
    float: left;
    list-style: none;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    margin: 3px;
    cursor: pointer;
}
.pen-color li.active::before{
    position: absolute;
    left: 1.4px;
    top: 1.3px;
    content: '';
    display: block;
    width: 18px;
    height: 18px;
    background: transparent;
    border: 2px solid #fff;
    border-radius: 50%;
}
